
function Divider() {
      return (
            <span style={{
                  display: "block",
                  width: "100%",
                  borderBottom: "1px solid #ddd",
                  marginLeft: "auto",
                  marginRight: "auto",
                  marginBottom: "12px",
                  opacity: 0.6
            }}></span>
      )
}

export default Divider;